import React from 'react'
import { Link } from 'gatsby'
import { StaticImage } from 'gatsby-plugin-image'

const Projects = () => {
  return (
    <section id='projects' className='my-12 scroll-mt-32 p-6'>
      <h2 className='mb-6 text-center text-4xl font-bold text-slate-900 dark:text-white sm:text-5xl'>
        üíºMy Projects
      </h2>
      <ul className='mx-auto my-12 flex list-none flex-col items-center gap-8 sm:flex-row'>
        <Link
          to='/projects/scenario-based-learning'
          className='flex w-2/3 flex-col rounded-3xl border border-solid border-slate-900 bg-white py-6 px-4 shadow-xl dark:border-gray-900 dark:bg-black sm:w-4/6 hover:bg-orange-200 text-slate-900 dark:text-white dark:hover:text-slate-600'
        >
          <li className='flex flex-col items-center'>
            <StaticImage
              src='../images/assets/project_icons/scenario_based_learning.png'
              alt='Scenario-based Learning'
              className='mb-6 w-1/2'
            />
            <h3 className='text-center text-2xl'>Scenario-Based Learning</h3>
            {/* <p className='mt-2 text-center text-xl text-slate-500 dark:text-slate-400 sm:hidden'>
              Affordable exploration
            </p> */}
          </li>
        </Link>
        <Link
          to='/projects/product-training-orientation'
          className='flex w-2/3 flex-col rounded-3xl border border-solid border-slate-900 bg-white py-6 px-4 shadow-xl dark:border-gray-900 dark:bg-black sm:w-4/6 hover:bg-orange-200 text-slate-900 dark:text-white dark:hover:text-slate-600'
        >
          <li className='flex flex-col items-center'>
            <StaticImage
              src='../images/assets/project_icons/product_orientation.png'
              alt='Product Training and Orientation'
              className='mb-6 w-1/2'
            />
            <h3 className='text-center text-2xl'>
              Product Training and Orientation
            </h3>
            {/* <p className='mt-2 text-center text-xl text-slate-500 dark:text-slate-400 sm:hidden'>
              Best-selling rocket
            </p> */}
          </li>
        </Link>
        <Link
          to='/projects/adaptive-quiz-branching'
          className='flex w-2/3 flex-col rounded-3xl border border-solid border-slate-900 bg-white py-6 px-4 shadow-xl dark:border-gray-900 dark:bg-black sm:w-4/6 hover:bg-orange-200 text-slate-900 dark:text-white dark:hover:text-slate-600'
        >
          <li className='flex flex-col items-center'>
            <StaticImage
              src='../images/assets/project_icons/adaptive_quiz.png'
              alt='Adaptive Quiz with Branching'
              className='mb-6 w-1/2'
            />
            <h3 className='text-center text-2xl'>
              Adaptive Branching Quiz Prototype
            </h3>
            {/* <p className='mt-2 text-center text-xl text-slate-500 dark:text-slate-400 sm:hidden'>
              Luxury Starship
            </p> */}
          </li>
        </Link>
      </ul>
    </section>
  )
}

export default Projects
