import React from 'react'
import { StaticImage } from 'gatsby-plugin-image'

const Hero = () => {
  return (
    <section
      id='hero'
      className='mb-12 flex scroll-mt-20 flex-col-reverse items-center justify-center gap-8 p-6 sm:flex-row'
    >
      <article className='sm:w-1/2'>
        <h2 className='max-w-md text-center text-4xl font-bold text-slate-900 dark:text-slate-400 sm:text-left sm:text-5xl'>
          Hi, I'm&nbsp;
          <span className='text-orange-500 dark:text-white'>Ankeet.</span>
        </h2>
        <p className='mt-4 max-w-md text-center text-2xl text-slate-700 dark:text-slate-400 sm:text-left'>
          I thrive at the intersection of&nbsp;
          <span className='font-semibold text-orange-500 dark:text-white'>
            training, content development, eLearning and instructional design.
          </span>
        </p>
        {/* <p className='mt-4 max-w-md text-center text-2xl text-slate-700 dark:text-slate-400 sm:text-left'>
          Think Ankeet, Think eLearning.
        </p> */}
      </article>
      <StaticImage
        className='w-1/2 rounded-full shadow-md'
        src='../images/assets/profile_images/hero_image.png'
        alt="Ankeet Patel's profile picture"
      />
    </section>
  )
}

export default Hero
